<template>
  <div class="useritem">
    <!-- 用户内容 -->
    <div class="list">
      <ul>
        <li
          v-for="item in userdata"
          :key="item.id"
          @click="lookInside(item.id)"
        >
          <div class="title">{{ item.title }}</div>
          <div class="txt">{{ item.text }}</div>
        </li>
      </ul>
    </div>
    <!-- 页码 -->
    <div class="pageNumberBox" v-show="allCurrenPage">
      <ul class="pageNumber">
        <li
          v-for="(item, index) in pages"
          :key="index"
          @click="active(item)"
          :class="{ activex: item === currenPage }"
        >
          {{ item }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "homeCP",
  data() {
    return {
      userdata: [],
      // 页码 当前页码位置
      currenPage: 1,
      // 总页码
      allCurrenPage: 1,
    };
  },
  computed: {
    ...mapState({
      userinput: (state) => state.homeState.userinput,
    }),
    pages() {
      // 根据当前页码的位置展示不同的翻页器样式
      const c = this.currenPage;
      const t = this.allCurrenPage;

      let ax = 1;
      if (t <= 5) {
        const arra = [];
        for (var i = 1; i < t; i++) {
          ax = t - (t - i);
          arra.push(ax);
        }
        arra[arra.length] = t;
        return arra;
      } else if (t > 5) {
        if (c <= 3) {
          return [1, 2, 3, 4, "...", t];
        } else if (c >= t - 3) {
          return [1, "...", t - 4, t - 3, t - 2, t - 1, t];
        } else {
          return [1, "...", c - 2, c - 1, c, c + 1, c + 2, "...", t];
        }
      }
    },
  },
  mounted() {
    this.userdata = this.userinput.slice(0, 10);
    this.allCurrenPage = Math.ceil(this.userinput.length / 10);
  },
  methods: {
    lookInside(id) {
      this.$store.dispatch("getlogin", id);
      this.$router.push({ name: "v", params: { id: id } });
      this.$bus.$emit("sendData", false);
    },
    active(item) {
      // 判断当前值是否是字符串 或等于当前页数
      if (typeof item == "string") return;
      // if (item === this.currenPage) return;
      this.currenPage = item;
      let oldArr = this.userinput;
      let newArr = [];
      newArr = oldArr.slice((item - 1) * 10, item * 10);
      this.userdata = newArr;
    },
  },
};
</script>

<style scoped>
.useritem {
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 0 20px;
  border-top: 2px solid #eaecc6;
}
.list {
  width: 50vw;
  margin: 0 auto;
}
.list .title {
  font: 700 24px "";
}
.list li {
  list-style: none;
  width: 100%;
  height: 200px;
  box-sizing: border-box;
  padding: 10px;
  border: 1px solid black;
  margin: 10px 0;
  overflow: hidden;
  border-radius: 13px;
}
.list li .title {
  margin: 10px 20px;
}
.list li .txt {
  box-sizing: border-box;
  padding: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
/* 页码 */
.pageNumberBox {
  display: flex;
  justify-content: center;
}
.pageNumber {
  display: flex;
  box-sizing: border-box;
  padding: 10px;
  user-select: none;
}
.pageNumber li {
  list-style: none;
  width: 50px;
  height: 50px;
  border-radius: 14px;
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.activex {
  background-color: red;
}
</style>
